import React, { useCallback, useState } from 'react';
import NftSelectTitle from '../../components/NftSelectTitle'
import NftSynthetic from '../../components/NftSynthetic' //蛋白质合成
import NftSolution from '../../components/NftSolution'  //蛋白质溶液
import NftTrays from '../../components/NftTrays'        //培养槽
import styles from './TNft.module.css'

function Nft() {
  const [selectId, setSelectId] = useState(1);
  
  const setelectClick = useCallback((id) =>{
    setSelectId(id)
  },[])
  let NftComponent = null;
  switch(selectId){
    case 1:
      NftComponent = <NftSynthetic />;
      break;
    case 2:
      NftComponent = <NftTrays />;
      break;
    case 3:
      NftComponent = <NftSolution />;
      break;
    default:
      break
  }

  return (
    <div>
      <NftSelectTitle setelectClick={setelectClick} selectId={selectId}/>
      {NftComponent}
      <div className={styles.bg}></div>
    </div>
  );
}

export default Nft;
