import React from 'react';
import styles from './SecondaryLevel.module.css'
import { Layer, Stage } from 'react-konva';
import StairProtein from '../../Protein/StairProtein'

function SecondaryLevel() {
  return (
    <div className={styles.upgrade}>
        <div className={styles.upgradeBtn}>合成<p>+1DMS</p></div>
        <Stage width={80} height={80} className={styles.proteinSize}>
        <Layer>
            <StairProtein proteinId={130} level={2}/>
        </Layer>
        </Stage>
        <Stage width={80} height={80} className={styles.proteinSize2}>
        <Layer>
            <StairProtein proteinId={230} level={2}/>
        </Layer>
        </Stage>
    </div>
  );
}

export default SecondaryLevel;
