import React, { useEffect, useRef, useState } from 'react';
import styles from './LockUp.module.css'

import { ContractHUSD, ContractStaking } from '../../../../utils/contract'
import web3 from '../../../../utils/initWeb3'
// import BigNumber from 'bignumber.js'
// import Loading from '../../../../components/Loading'
import { message } from 'antd';

function LockUp() {
  const inputRef = useRef<HTMLInputElement>(null);
  // const [loadingState, setLoadingState] = useState(false)
  const [balance, setBalance] = useState(0)
  const amount = '100000000000000000000000000';
  const [user, setUser] = useState(0)
  const [buttonId, setButtonId] = useState(0)

  useEffect(()=>{
    getBalanceOf()
    getUser()
    getAllowance()
  },[])

  const approveClick = async () => {
    const myAddress = await web3.eth.getAccounts()
    const approve = await ContractHUSD.methods.approve(ContractStaking._address,amount).send({
      from : myAddress[0]
    })
    console.log(approve)
  }

  const getAllowance =  async () => {
    const myAddress = await web3.eth.getAccounts()
    const allowance = await ContractHUSD.methods.allowance(myAddress[0],ContractStaking._address).call()
    console.log(allowance)
    setButtonId(allowance)
  }


  const getBalanceOf = async () =>{
    const myAddress = await web3.eth.getAccounts()
    const data = await ContractHUSD.methods.balanceOf(myAddress[0]).call()
    const data2 = web3.utils.fromWei(data, 'mwei');
    if(data2 > 0){
      setBalance(data2/100)
    }
  }
  // 在指定矿池存入质押通证 锁仓
  const lockClick = async () =>{
    const myAddress = await web3.eth.getAccounts()
    const val: string = inputRef.current!.value.trim();
    if(!val){
     message.error('请输入质押数量')
    }else{
      const numberVal = web3.utils.toWei(val)
      const numberValUrl = numberVal.substring(0, numberVal.length-10);
     if(balance  >= Number(val) ){
      // setLoadingState(true)
       const lock = await ContractStaking.methods.stake("2",numberValUrl).send({
       from:myAddress[0]
     })
     if(lock.status){
      //  setLoadingState(false)
       message.success('操作成功')
     }
     }else{
       message.error('余额不足')
       inputRef.current!.value = ''
     }
    }
 }
  // 取回我在指定矿池质押的通证及收益 锁仓
  const exitClick = async () => {
    // setLoadingState(true)
    const myAddress = await web3.eth.getAccounts()
    const exit = await ContractStaking.methods.exit("2").send({
      from:myAddress[0]
    })
    if(exit){
      // setLoadingState(false)
      message.error('操作成功')
    }
  }

  const getUser = async ()  => {
    const myAddress = await web3.eth.getAccounts()
    const data = await ContractStaking.methods.users("2",myAddress[0]).call()
    console.log(data)
    const data2 = web3.utils.fromWei(data.shares,'mwei');
    console.log(data2)
    if(data2 > 0){
      setUser(data2/100)
    }
  }

  return (
    <div className={styles.bg}>
        {/* <Loading state={loadingState} name='请求中...'/> */}
        <div className={styles.swap}>HUSD</div>
        <input className={styles.swapInput} type="number" name="swapInput" placeholder='请输入质押数量' ref={inputRef}></input>
        <div className={styles.balanceFlex}>
            <div className={styles.balance}>
                <p>钱包余额</p>
                <p>{balance}HUSD</p>
                <button className={styles.btn} onClick={ buttonId > 0 ? lockClick : approveClick}>{buttonId > 0 ? "锁仓" : "授权"}</button>
            </div>
            <div className={styles.number}>
                <p>质押份额</p>
                <p>{user}HUSD</p>
                <button className={styles.btn} onClick={exitClick}>解锁</button>
            </div>
        </div>
    </div>
  );
}

export default LockUp;
