import React, { useState } from 'react';
import styles from './NftUpgrade.module.css'
import SecondaryLevel from './SecondaryLevel'
import TertiaryLevel from './TertiaryLevel'
import FourLevel from './FourLevel'

function NftUpgrade() {

  const [checkId, setCheckId] = useState(1);

  let LevelComponent = null;
  switch(checkId){
    case 1:
      LevelComponent = <SecondaryLevel />;
      break;
    case 2:
      LevelComponent = <TertiaryLevel />;
      break;
    case 3:
      LevelComponent = <FourLevel />;
      break;
    default:
      break;
  }
  return (
    <div className={styles.bg}>
      <div className={styles.flexCheck}>
            <div className={checkId !== 1 ? styles.uncheck : styles.check} onClick={()=>setCheckId(1)}>初级结构</div>
            <div className={checkId !== 2 ? styles.uncheck : styles.check} onClick={()=>setCheckId(2)}>次级结构</div>
            <div className={checkId !== 3 ? styles.uncheck : styles.check} onClick={()=>setCheckId(3)}>三级结构</div>
        </div>
      {LevelComponent}
    </div>
  );
}

export default NftUpgrade;
