import React from 'react';
import styles from './TertiaryLevel.module.css'
import { Layer, Stage } from 'react-konva';
import SecondaryProtein from '../../Protein/SecondaryProtein'

function TertiaryLevel() {
  return (
    <div className={styles.upgrade}>
        <div className={styles.upgradeBtn}>合成<p>+1DMS</p></div>
        <Stage width={80} height={80} className={styles.proteinSize}>
            <Layer>
                <SecondaryProtein proteinId={30} level={1}/>
            </Layer>
        </Stage>
        <Stage width={80} height={80} className={styles.proteinSize2}>
            <Layer>
                <SecondaryProtein proteinId={70} level={1}/>
            </Layer>
        </Stage>
        <Stage width={80} height={80} className={styles.proteinSize3}>
            <Layer>
                <SecondaryProtein proteinId={230} level={1}/>
            </Layer>
        </Stage>
    </div>
  );
}

export default TertiaryLevel;
