import React, { useCallback, useState } from 'react';
import styles from './NftTrays.module.css'

import NftSelect from "../NftSelect"
import TrayOne from './TrayOne'
import TrayTwo from './TrayTwo'
import TrayThree from './TrayThree'
import TrayFour from './TrayFour'

function NftTrays() {
  const [checkId, setCheckId] = useState(1);
  const setCheckClick = useCallback((id) =>{
      setCheckId(id)
    },[])
  let TrayComponent = null;
  switch(checkId){
    case 1:
      TrayComponent = <TrayOne />;
      break;
    case 2:
      TrayComponent = <TrayTwo />;
      break;
    case 3:
      TrayComponent = <TrayThree />;
      break;
    case 4:
      TrayComponent = <TrayFour />;
      break;
    default:
      break;
  }
return (
    <div>
      <div className={styles.title}>
        <p> <span className={styles.titleNumber}>注意：</span> 蛋白质在培养槽内每天减少10点效率，效率为0的蛋白质无法再生产DMS。可用于合成新的蛋白质。</p>
      </div>
      <div className={styles.bg}>
          <NftSelect setCheckClick={setCheckClick} checkId={checkId}/>
          <div>
            {TrayComponent}
          </div>
      </div>
    </div>
  );
}

export default NftTrays;
