import React from 'react';
import styles from './TrayModal.module.css'
import ModalButtom from '../../Button/ModalButtom'


function TrayModal(props: { upgradeClick: any }){

    const {upgradeClick} = props

  return (
    <div className={styles.modal}>
        <div className={styles.bg}>
            <div className={styles.trayBg}></div>
            <div className={styles.trayName}>
                <h3>初级结构培养槽</h3>
                <p>经验值：<span>10000</span> </p>
                <p>挖矿效率：<span>100</span> </p>
            </div>
            <div className={styles.flexBtn}>
                <div className={styles.cancelBtn}>
                    <ModalButtom onClick={ () => upgradeClick(0)} name="一键升级" color="green"/>
                </div>
                <div className={styles.transferBtn}>
                    <ModalButtom onClick={ () => upgradeClick(0)} name="升级" color="orange"/>
                </div>
            </div>
            <div className={styles.trayText}>当前等级升级到下一级需要经验值为200 一键升级：升级到当前能升最高等级。</div>
        </div>
    </div>
  );
}
export default TrayModal;
