import React from 'react';
import styles from './LiquidityTitle.module.css'

function LiquidityTitle() {
  return (
    <div className={styles.title}>
      <div className={styles.method}>DMS流动性挖矿</div>
        <div className={styles.text}>
            <p>存入您的流动性提供商Token以接收DMS。</p>
        </div>
    </div>
  );
}

export default LiquidityTitle;
