import React, { useCallback, useEffect, useState } from 'react';
import {Route, NavLink ,Switch, Redirect} from 'react-router-dom'
import { Row, Col } from 'antd';

import Dig from './pages/Dig'
import Reward from './pages/Reward'
// import Mall from './pages/Mall'
import Nft from './pages/Nft'
// import User from './pages/User'
import Login from './pages/Login'
// import Etp from './pages/Etp'

import Background from './assets/images/bg.png';
import logo from './assets/images/logo.png'

import web3 from './utils/initWeb3'

const innerHeight = window.innerHeight + 'px'

const AppStyle = {
  height: innerHeight,
}

const BackgroundStyle = {
  width: "100%",
  minHeight: innerHeight,
  backgroundImage: `url(${Background})` ,
};


function App() {

  const [accounts, setAccounts] = useState([]);
  
  useEffect(()=>{
    getAccounts()
  },[])
  
  async function getAccounts() {
    const accounts = await web3.eth.getAccounts()
    setAccounts(accounts)
  }
  const state = useCallback((id) =>{
    setAccounts(id)
  },[])

  return (
    <>
     <div style={AppStyle}>
       <div style={BackgroundStyle}>
         <header className='header-nav'>
         <Row>
            <Col xs={6} sm={6} md={6} lg={6} xl={6}>
              <NavLink  activeClassName="activeNavBar" className='list-nav' to="/dig">挖矿</NavLink>
            </Col>
            {/* <Col xs={4} sm={4} md={4} lg={12} xl={4}>
              <NavLink  activeClassName="activeNavBar" className='list-nav' to="/etp">ETP充值</NavLink>
            </Col> */}
            <Col xs={6} sm={6} md={6} lg={6} xl={6}>
              <NavLink  activeClassName="activeNavBar" className='list-nav' to="/reward">奖励</NavLink>
            </Col>
            {/* <Col xs={4} sm={4} md={4} lg={4} xl={4}>
              <NavLink  activeClassName="activeNavBar" className='list-nav' to="/mall">商城</NavLink>
            </Col> */}
            <Col xs={6} sm={6} md={6} lg={6} xl={6}>
              <NavLink  activeClassName="activeNavBar" className='list-nav' to="/nft">蛋白质</NavLink>
            </Col>
            {/* <Col xs={6} sm={6} md={6} lg={6} xl={6}>
              <NavLink  activeClassName="activeNavBar" className='list-nav' to="/user">我的</NavLink>
            </Col> */}
            <Col xs={6} sm={6} md={6} lg={6} xl={6}>
               <img src={logo} alt="Logo" className='Wallet-img'/>
            </Col>
          </Row>
         </header>
         <section className='section-div'>
              {
                accounts.length >= 1
                ?
                (
                  <div>
                    <Switch>
                      <Route path="/dig" component={Dig}/>
                      {/* <Route path="/etp" component={Etp}/> */}
                      <Route path="/reward" component={Reward}/>
                      {/* <Route path="/User" component={User}/> */}
                      {/* <Route path="/mall" component={Mall}/> */}
                      <Route path="/nft" component={Nft}/>
                      <Redirect to="/dig"/>
                    </Switch>
                </div>
               )
                :
                (
                  <div>
                    <Login state={state}/>
                  </div>
                )
              }
         </section>
       </div>
     </div>
    </>
  );
}

export default App;
