import React from 'react';
import styles from './NftSelectTitle.module.css'

interface DigSelectProps {
    selectId : number;
    setelectClick : (checkId:number) => void;
}

function NftSelectTitle(props: DigSelectProps) {
    const {selectId,setelectClick} = props;
  return (
    <div >
        <div className={styles.flexCheck}>
            <div className={selectId !== 1 ? styles.uncheck : styles.check} onClick={() => setelectClick(1)}>蛋白质合成</div>
            <div className={selectId !== 2 ? styles.uncheck : styles.check} onClick={() => setelectClick(2)}>培养槽</div>
            <div className={selectId !== 3 ? styles.uncheck : styles.check} onClick={() => setelectClick(3)}>蛋白质溶液</div>
        </div>
    </div>
  );
}

export default NftSelectTitle;
