import React, { useState } from 'react';
import styles from './LockDig.module.css'
import DMS from '../DMS'
import ETP from '../ETP'
import HUSD from '../HUSD'

function LockDig() {

  const [checkId, setCheckId] = useState(0);

  let LevelComponent = null;
  switch(checkId){
    case 0:
      LevelComponent = <DMS />;
      break;
    case 1:
      LevelComponent = <ETP />;
      break;
    case 2:
      LevelComponent = <HUSD />;
      break;
    default:
      break;
  }
  return (
    <div className={styles.bg}>
      <div className={styles.flexCheck}>
            <div className={checkId !== 0 ? styles.uncheck : styles.check} onClick={()=>setCheckId(0)}>DMS质押</div>
            <div className={checkId !== 1 ? styles.uncheck : styles.check} onClick={()=>setCheckId(1)}>ETP质押</div>
            <div className={checkId !== 2 ? styles.uncheck : styles.check} onClick={()=>setCheckId(2)}>HUSD质押</div>
        </div>
      {LevelComponent}
    </div>
  );
}

export default LockDig;
