import React from 'react';
import styles from './NftSelect.module.css'

interface NftSelectProps {
    checkId : number;
    setCheckClick : (checkId:number) => void;
}

function NftSelect(props: NftSelectProps) {
    const {checkId,setCheckClick} = props;
  return (
    <div >
        <div className={styles.flexCheck}>
            <div className={checkId !== 1 ? styles.uncheck : styles.check} onClick={() => setCheckClick(1)}>初级结构</div>
            <div className={checkId !== 2 ? styles.uncheck : styles.check} onClick={() => setCheckClick(2)}>次级结构</div>
            <div className={checkId !== 3 ? styles.uncheck : styles.check} onClick={() => setCheckClick(3)}>三级结构</div>
            <div className={checkId !== 4 ? styles.uncheck : styles.check} onClick={() => setCheckClick(4)}>四级结构</div>
        </div>
    </div>
  );
}

export default NftSelect;
