import React from 'react';
import Konva from 'konva';
import useImage from 'use-image';
import { Image } from 'react-konva';
import URL_3 from '../../assets/images/protein/3.png'


const TertiaryProtein = (props: { proteinId: number; level:number }) => {
  const {proteinId} = props;
  const [image] = useImage(URL_3, 'Anonimus');
  const imageRef:any = React.useRef();
  React.useEffect(() => {
    if (image) {
      imageRef.current.cache();
      imageRef.current.hue(proteinId);
      // imageRef.current.saturation(0);
      // imageRef.current.red(150);
      // imageRef.current.green(225);
      // imageRef.current.blue(225);
    }
  }, [image,proteinId]);
  return (
    <Image
      ref={imageRef}
      x={0}
      y={0}
      width={80}
      height={80}
      image={image}
      filters={[Konva.Filters.HSL]}
      blurRadius={10}
    />
  );
};

export default TertiaryProtein;