import React from 'react';
import LiquidityTitle from './LiquidityTitle/index'
import LiquiditySwap from './LiquiditySwap/index'
import styles from './DigLiquidity.module.css'

function DigLiquidity() {
  return (
    <div>
      <LiquidityTitle />
      <div className={styles.title}>Participating pools</div>
      <LiquiditySwap />
      <LiquiditySwap />
    </div>
  );
}

export default DigLiquidity;
