import React, { useCallback, useState } from 'react';
import { Layer, Stage } from 'react-konva';
import FourProtein from '../../Protein/FourProtein'  //一级蛋白质结构
import styles from './TrayFour.module.css'

import TransferModal from '../../Modal/TransferModal'

function TrayFour() {
 
    const [checkId, setCheckId] = useState(1);
    const [openId, setOpenId] = useState(0);


    const setCheckClick = (id:number) =>{
        setCheckId(id)
    }
  
    const openClick = useCallback((id: number): void =>{
          setOpenId(id)
    },[])

  return (
    <div className={styles.bg}>
        <div className={styles.bgFlex}>
            <div className={styles.proteinColor} onClick={() => setCheckClick(1)}>
                <div className={styles.proteinName} >
                    <Stage width={80} height={80}>
                        <Layer>
                        <FourProtein proteinId={12} level={1}/>
                        </Layer>
                    </Stage>
                </div>
                <div className={checkId === 1 ? styles.proteinCheck : styles.proteinUnCheck} onClick={ () => setOpenId(1)}>
                能力10
                </div>
            </div>
            <div className={styles.proteinColor} onClick={() => setCheckClick(1)}>
                <div className={styles.proteinName} >
                    <Stage width={80} height={80}>
                        <Layer>
                        <FourProtein proteinId={22} level={1}/>
                        </Layer>
                    </Stage>
                </div>
                <div className={checkId === 1 ? styles.proteinCheck : styles.proteinUnCheck} onClick={ () => setOpenId(1)}>
                能力10
                </div>
            </div>
            <div className={styles.proteinColor} onClick={() => setCheckClick(1)}>
                <div className={styles.proteinName} >
                    <Stage width={80} height={80}>
                        <Layer>
                        <FourProtein proteinId={32} level={1}/>
                        </Layer>
                    </Stage>
                </div>
                <div className={checkId === 1 ? styles.proteinCheck : styles.proteinUnCheck} onClick={ () => setOpenId(1)}>
                能力10
                </div>
            </div>
            <div className={styles.proteinColor} onClick={() => setCheckClick(1)}>
                <div className={styles.proteinName} >
                    <Stage width={80} height={80}>
                        <Layer>
                        <FourProtein proteinId={42} level={1}/>
                        </Layer>
                    </Stage>
                </div>
                <div className={checkId === 1 ? styles.proteinCheck : styles.proteinUnCheck} onClick={ () => setOpenId(1)}>
                  能力：1
                </div>
            </div>
            <div className={styles.proteinColor} onClick={() => setCheckClick(1)}>
                <div className={styles.proteinName} >
                    <Stage width={80} height={80}>
                        <Layer>
                        <FourProtein proteinId={52} level={1}/>
                        </Layer>
                    </Stage>
                </div>
                <div className={checkId === 1 ? styles.proteinCheck : styles.proteinUnCheck} onClick={ () => setOpenId(1)}>
                  能力：1
                </div>
            </div>
            <div className={styles.proteinColor} onClick={() => setCheckClick(1)}>
                <div className={styles.proteinName} >
                    <Stage width={80} height={80}>
                        <Layer>
                        <FourProtein proteinId={62} level={1}/>
                        </Layer>
                    </Stage>
                </div>
                <div className={checkId === 1 ? styles.proteinCheck : styles.proteinUnCheck} onClick={ () => setOpenId(1)}>
                  能力：1
                </div>
            </div>
        </div>
        <div className={styles.pag}>
            <button className={styles.unPagBtn}>上一页</button>
            <button className={styles.upgradeBtn}>升级培养槽<p>等级1</p></button>
            <button className={styles.pagBtn}>下一页</button>
        </div>
        {
         openId === 1
         && 
         (<TransferModal openClick={openClick} />)
        }
    </div>
  );
}

export default TrayFour;
