import React from 'react';
import styles from './ModalButton.module.css'

function ModalButton(props: { onClick: any; name:string; color:string }) {
   
  const {onClick,name,color } = props

  return (
    <div>
        <button className={color === "orange" ? styles.orangeBtn : styles.greenBtn } onClick={()=>onClick()}>{name}</button>
    </div>
  );
}

export default ModalButton;
