import React from 'react';
import styles from './DigSelect.module.css'

interface DigSelectProps {
    checkId : boolean;
    setCheckClick : (checkId:boolean) => void;
    name1 : string;
    name2 : string;
}

function DigSelect(props: DigSelectProps) {
    const {checkId,setCheckClick,name1,name2} = props;
  return (
    <div >
        <div className={styles.flexCheck}>
            <div className={ checkId === true ? styles.uncheck : styles.check} onClick={() => setCheckClick(false)}>{name1}</div>
            <div className={checkId === false ? styles.uncheck : styles.check} onClick={() => setCheckClick(false)}>{name2}</div>
        </div>
    </div>
  );
}

export default DigSelect;
