import React from 'react';
import styles from './SolutionModal.module.css'
import ModalButtom from '../../Button/ModalButtom'


function SolutionModal(props: { openClick: any }) {
    const {openClick} = props
  return (
    <div className={styles.modal}>
        <div className={styles.bg}>
            <div className={styles.solutionBg}></div>
            <div className={styles.solutionText}>蛋白质溶液可随机获得蛋白质能力</div>
            <div className={styles.solutionInput}>
                <p>对方的钱包地址</p>
                <input type="text" name="address" />
            </div>
            <div className={styles.flexBtn}>
                <div className={styles.cancelBtn}>
                    <ModalButtom onClick={ () => openClick(0)} name="转让" color="green"/>
                </div>
                <div className={styles.transferBtn}>
                    <ModalButtom onClick={ () => openClick(0)} name="获得" color="orange"/>
                </div>
            </div>

        </div>
    </div>
  );
}
export default SolutionModal;
