import React, { useEffect, useState } from 'react';
import styles from './RewardQR.module.css'
import QRCode  from 'qrcode.react';
import web3 from '../../utils/initWeb3'

function RewardQR() {

  const [address, setAddress] = useState(String)
  // const [share, setShare] = useState(String)
  
  useEffect(()=>{
    getMyAddress()
  },[])

  const getMyAddress = async () => {
    const myAddress = await web3.eth.getAccounts()
    setAddress(myAddress[0])
  };
  return (
      <div className={styles.bg}>
        <div className={styles.title}>二维码地址</div>
        <div className={styles.qr}>
            <QRCode
            value="https://dmschain.com/"  //value参数为生成二维码的链接
            size={160} //二维码的宽高尺寸
            fgColor="#000000"  //二维码的颜色
            />
        </div>
        <div className={styles.address}>
          {address}
        </div>
        {/* <div>
            <button className={styles.copyBtn}>复制地址</button>
            <button className={styles.copyBtn}>分享</button>
        </div> */}
    </div>
  );
}

export default RewardQR;
