import React from 'react';
import styles from './RewardTitle.module.css'

function RewardTitle() {
  return (
    <div className={styles.title}>
        <div className={styles.method}>NFT奖励方法</div>
        <div className={styles.text}>
            <p>每邀请一个新的地址即可随机获得一个NFT资产<span>(初级机构蛋白质)</span></p>
        </div>
    </div>
  );
}

export default RewardTitle;
