import React, { useCallback, useState } from 'react';
import styles from './NftSolution.module.css'
import SolutionModal from '../Modal/SolutionModal'

function NftSolution() {
  const [openId, setOpenId] = useState(0);
  const openClick = useCallback((id: number): void =>{
    setOpenId(id)
},[])

  return (
    <div>
      <div className={styles.title}>
        <p>点击蛋白质溶液可获得随机能力值的蛋白质，</p>
        <p className={styles.titleNumber}>你已有6个蛋白质溶液了，赶快来打开吧！</p>
      </div>
      <div className={styles.bg}>
        <div className={styles.bgColor}>
          <div className={openId === 1 ? styles.solutionCheck : styles.solution } onClick={()=>setOpenId(1)}></div>
          <div className={openId === 1 ? styles.solutionCheck : styles.solution } onClick={()=>setOpenId(1)}></div>
        </div>
      </div>
      <div className={styles.buyImg}></div>
      {
        openId === 1
        && 
        (<SolutionModal openClick={openClick} />)
      }
    </div>
  );
}

export default NftSolution;
