import React, { useCallback, useState } from 'react';
import { Layer, Stage } from 'react-konva';
import StairProtein from '../../Protein/StairProtein'  //一级蛋白质结构
import styles from './TrayOne.module.css'

import TransferModal from '../../Modal/TransferModal'
import TrayModal from '../../Modal/TrayModal'

function TrayOne() {
 
    const [checkId, setCheckId] = useState(1);
    const [openId, setOpenId] = useState(0);
    const [upgradeId, setUpgradeId] = useState(0);


    const setCheckClick = (id:number) =>{
        setCheckId(id)
    }
  
    const openClick = useCallback((id: number): void =>{
          setOpenId(id)
    },[])

    const upgradeClick = useCallback((id : number): void =>{
        setUpgradeId(id)
    },[])

    

  return (
    <div className={styles.bg}>
        <div className={styles.bgFlex}>
            <div className={styles.proteinColor} onClick={() => setCheckClick(1)}>
                <div className={styles.proteinName} >
                    <Stage width={80} height={80}>
                        <Layer>
                        <StairProtein proteinId={12} level={1}/>
                        </Layer>
                    </Stage>
                </div>
                <div className={checkId === 1 ? styles.proteinCheck : styles.proteinUnCheck} onClick={ () => setOpenId(1)}>
                能力10
                </div>
            </div>
        </div>
        <div className={styles.pag}>
            <button className={styles.unPagBtn}>上一页</button>
            <button className={styles.upgradeBtn} onClick={()=>setUpgradeId(1)}>升级培养槽<p>等级1</p></button>
            <button className={styles.pagBtn}>下一页</button>
        </div>
        {
            openId === 1
            && 
         (<TransferModal openClick={openClick} />)
        }
        {
            upgradeId === 1
            && 
         (<TrayModal upgradeClick={upgradeClick} />)
        }
    </div>
  );
}

export default TrayOne;
