import React, { useEffect, useRef, useState } from 'react';
import styles from './LockUp.module.css'

import { ContractDMS, ContractStaking } from '../../../../utils/contract'
import web3 from '../../../../utils/initWeb3'
import { message } from 'antd';

function LockUp() {
  const inputRef = useRef<HTMLInputElement>(null);
  const [balance, setBalance] = useState(0)
  const [user, setUser] = useState(0)
  const amount = '100000000000000000000000000';
  const [buttonId, setButtonId] = useState(1)

  useEffect(()=>{
    getBalanceOf()
    getUser()
    getAllowance()
  },[])

  //ETP授权
  const approveClick = async () => {
    const myAddress = await web3.eth.getAccounts()
    const approve = await ContractDMS.methods.approve(ContractStaking._address,amount).send({
      from : myAddress[0]
    })
    console.log(approve)
  }

  //检查是否授权：未授权返回 0 ，已经授权返回：授权数量
  const getAllowance =  async () => {
    const myAddress = await web3.eth.getAccounts()
    const allowance = await ContractDMS.methods.allowance(myAddress[0],ContractStaking._address).call()
    setButtonId(allowance)
  }

  const getBalanceOf = async () =>{
    const myAddress = await web3.eth.getAccounts()
    const data = await ContractDMS.methods.balanceOf(myAddress[0]).call()
    const data2 = web3.utils.fromWei(data, 'ether');
    // if(data2.length > 12){
    //   const data3 = data2.toFixed(6)
    //   setBalance(data3)
    // }else{
      setBalance(data2)
    // }
  }

  const lockClick = async () =>{
    const myAddress = await web3.eth.getAccounts()
    const val: string = inputRef.current!.value.trim();

    if(balance >= Number(val)){
      console.log(balance)
      console.log(Number(val))

    }
    // if(!val){
    //  message.error('请输入质押数量')
    // }else{
    //  const numberVal = web3.utils.toWei(val)
    //  console.log(numberVal)
    //  if(balance  >= Number(val) ){
    //  const lock = await ContractStaking.methods.stake("0",numberVal).send({
    //    from:myAddress[0]
    // })
    //  if(lock.status){
    //    message.success('操作成功')
    //  }
    //  }else{
    //    message.error('余额不足')
    //    inputRef.current!.value = ''
    //  }
    // }
  }

  // 取回我在指定矿池质押的通证及收益 锁仓
  const exitClick = async () => {
    const myAddress = await web3.eth.getAccounts()
    const exit = await ContractStaking.methods.exit("0").send({
      from:myAddress[0]
    })
    if(exit){
      message.error('操作成功')
    }
  }

  const getUser = async ()  => {
    const myAddress = await web3.eth.getAccounts()
    const data = await ContractStaking.methods.users("0",myAddress[0]).call()
    const data2 = web3.utils.fromWei(data.shares,'ether');
    if(data2.length > 12){
      const data3 = data2.toFixed(6)
      setUser(data3)
    }else{
      setUser(data2)
    }
  }
  return (
    <div className={styles.bg}>
        <div className={styles.swap}>DMS</div>
        <input className={styles.swapInput} type="number" name="swapInput" placeholder='请输入质押数量' ref={inputRef}></input>
        <div className={styles.balanceFlex}>
            <div className={styles.balance}>
                <p>钱包余额</p>
                <p>{balance}DMS</p>
                {/* <button className={styles.btn} onClick={ buttonId > 0 ? lockClick : approveClick}>{buttonId > 0 ? "锁仓" : "授权"}</button> */}
                <button className={styles.btn} onClick={ lockClick }>锁仓</button>
            </div>
            <div className={styles.number}>
                <p>质押份额</p>
                <p>{user}DMS</p>
                <button className={styles.btn} onClick={exitClick}>解锁</button>
            </div>
        </div>
    </div>
  );
}

export default LockUp;
