import React, { useEffect, useState } from 'react';
import styles from './PricesStep.module.css'

import { ContractStaking } from '../../../../utils/contract'
import web3 from '../../../../utils/initWeb3'

function PricesStep() {
  const [pool, setstate] = useState(0)
  const [earned, setEarned] = useState(Number)

  useEffect(()=>{
    getPoolClick()
    getEarned()
  },[])

  //返回矿池详情
  const getPoolClick = async () =>{
    const data=  await ContractStaking.methods.pools(2).call()
    const data2 = web3.utils.fromWei(data.totalShares, 'mwei');
    setstate(data2/100)
  }
  //返回指定矿池的账户未领收益
  const getEarned = async () => {
    const myAddress = await web3.eth.getAccounts()
    const data = await ContractStaking.methods.earned("2",myAddress[0]).call()
    const dataNumber = web3.utils.fromWei(data, 'ether');
    setEarned(dataNumber)
  }



  return (
    <div className={styles.step}>
        <div className={styles.stepTitle}>详情</div>
        <div className={styles.stepNumber}>
          <h3>结束时间：<span>已结束</span> </h3>
          <h3>锁仓总额：<span>{pool}</span> </h3>
          {/* <h3>奖励总量：<span>20000</span> </h3> */}
          {/* <h3>未领取收益：<span>{earned}</span> </h3> */}
        </div>

    </div>
  );
}

export default PricesStep;
