import React from 'react';
import styles from './Login.module.css'
import { message } from 'antd';
import {initWeb3,web3Modal,subscribeProvider  } from '../../utils/web3Model'


function Login(props: any) {
  const {state} = props

  const onConnectMetaMask = async (): Promise<void> => {
    const provider = await web3Modal.connect();
    await subscribeProvider(provider);
    const web3: any = initWeb3(provider);
    const accounts = await web3.eth.getAccounts();
    const address = accounts[0];
    console.log(address)
    if(address !== null && address){
      message.success('连接成功');
      state(address)
    }
  };
//Walletconnect登录
  // const onConnectWalletconnect = async (): Promise<void> => {
  //   const provider = await web3Modal.connectTo('walletconnect');
  //   await subscribeProvider(provider);
  //   const web3: any = initWeb3(provider);
  //   const accounts = await web3.eth.getAccounts();
  //   const address = accounts[0];
  //   console.log(address)
  // }
  return (
    <div className={styles.bg}>
        <h1>DMS健康实验室</h1>
        <div className={styles.borderColor}></div>
        <p>选择你要链接的钱包地址，以便你能更好的体验页面操作哦！</p>
        <button onClick={onConnectMetaMask}>链接钱包</button>
    </div>
  );
}

export default Login;


