import React from 'react';
import styles from './LiquiditySwap.module.css'
import swaoLogo from '../../../assets/images/dig/icon/1.png'

function LiquiditySwap() {
  return (
    <div>
       <div className={styles.bg}>
         <div className={styles.icon}>
            <img src={swaoLogo} alt="Logo" />
            <img src={swaoLogo} alt="Logo" />
         </div>
         <div className={styles.swap}>DMS—USDT</div>
         <div className={styles.deposit}>
           <button className={styles.btn}>Deposit</button>
         </div>
       </div>
    </div>
  );
}

export default LiquiditySwap;
