import React from 'react';
import { useState } from 'react';
import { useCallback } from 'react';
import DigSelect from '../../components/DigSelect'
// import DigLockUp from '../../components/DigLockUp' 
import DigLiquidity from '../../components/DigLiquidity'
import LockDig from './LockDig'
// import DigDeposit from '../../components/DigDeposit'
// import DigPrices from '../../components/DigPrices'

function Dig() {

  const [checkId, setCheckId] = useState(false);
  const setCheckClick = useCallback((id) =>{
    setCheckId(id)
  },[])
  return (
    <div>
       <DigSelect setCheckClick={setCheckClick} checkId={checkId} name1="锁仓挖矿" name2="流动性挖矿"/>
       {
         checkId === false
         ? 
         (<LockDig />)
         :
         (<DigLiquidity />)
        }
    </div>
  );
}

export default Dig;
