import React from 'react';
import styles from './FourLevel.module.css'
import { Layer, Stage } from 'react-konva';
import TertiaryProtein from '../../Protein/TertiaryProtein'


function FourLevel() {
  return (
    <div className={styles.upgrade}>
        <div className={styles.upgradeBtn}>合成<p>+1DMS</p></div>
        <Stage width={80} height={80} className={styles.proteinSize}>
          <Layer>
              <TertiaryProtein proteinId={130} level={3}/>
          </Layer>
        </Stage>
        <Stage width={80} height={80} className={styles.proteinSize2}>
          <Layer>
              <TertiaryProtein proteinId={10} level={3}/>
          </Layer>
        </Stage>
        <Stage width={80} height={80} className={styles.proteinSize3}>
          <Layer>
              <TertiaryProtein proteinId={60} level={3}/>
          </Layer>
        </Stage>
        <Stage width={80} height={80} className={styles.proteinSize4}>
          <Layer>
              <TertiaryProtein proteinId={250} level={3}/>
          </Layer>
        </Stage>
    </div>
  );
}

export default FourLevel;
